.row-fluid{display: -webkit-box; display: -ms-flexbox; display: flex; -ms-flex-wrap: wrap; flex-wrap: wrap; margin-right: -15px;margin-left: -15px;}

/* Popup */

.r_popup-overlay {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.5)
}

.r_popup {
    max-width: 90vw;
    max-height: 90vh;
    width: fit-content;
    width: -moz-fit-content;
    position: relative;
    top: 50%;
    -ms-transform: translateY(-50%);
    transform: translateY(-50%);
    margin: auto;
    background-color: #fff;
    padding: 10px;

    iframe {
        width: 90vh;
        height: 85vh;
        border: 0;
    }

    img {
        max-height: 85vh;
        max-width: 90vw;
    }
}

.img-popup {
    cursor: pointer;
}

/* Image cliquable */

.imgclick {
    text-align: center;
    position: relative;
    overflow: hidden;
    width: fit-content;
    width: -moz-fit-content;
    
    img {
        -webkit-transition: all 0.4s linear;
        transition: all 0.4s linear;
    }

    a {
        display: block;
        color: #fff;
    }

    .imgclickcontent {
        display: none;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
    }
}

.imgclick:hover img {
    transform: scale(1.1);
    filter: brightness(50%);
}

.imgclick:hover .imgclickcontent {
    display: block;
}